.corporate__breadcrumbs { margin: 14px auto 24px; }

.corporate .title-big { margin-bottom: 28px; text-align: center; }

.corporate__article { margin-bottom: 50px; }

.corporate__article:after { content: " "; display: table; clear: both; }

@media only screen and (max-width: 1023px) { .corporate__article { margin-bottom: 36px; } }

.corporate__article .title-middle { margin-bottom: 28px; }

@media only screen and (max-width: 767px) { .corporate__article .title-middle { text-align: center; margin-bottom: 12px; } }

.corporate__article .text-list { margin-bottom: 40px; }

@media only screen and (max-width: 1023px) { .corporate__article .text-list { margin-bottom: 12px; } }

.corporate__image { float: left; margin-left: 47px; margin-right: 56px; width: 256px; text-align: center; }

.corporate__image .text-blue { margin: 10px 30px 0; }

@media only screen and (max-width: 767px) { .corporate__image .text-blue { margin: 15px 0 0; } }

