@media only screen and (max-width: 767px) { .corporate__image { float: none; max-width: 300px; margin: 0 auto 20px; } }

.corporate__text { float: left; width: calc(100% - 256px - 47px - 56px); }

@media only screen and (max-width: 1023px) { .corporate__text { width: calc(100% - 256px - 24px - 24px); } }

@media only screen and (max-width: 767px) { .corporate__text { float: none; width: 100%; } }

.corporate__form-field { margin-top: 28px; }

