@media only screen and (max-width: 767px) { .corporate__form-field span { margin-left: 0; } }

.corporate__form-field .inline-form { margin-left: -10px; font-size: 0; }

@media only screen and (max-width: 767px) { .corporate__form-field .inline-form { margin-left: 0; } }

.text-list { font-family: "CorpidE1sSCd"; }

.text-list__item { margin-bottom: 9px; display: block; color: #3c3c3b; font-size: 20px; font-weight: 300; position: relative; padding-left: 16px; line-height: 1.15; }

.text-list__item:before { content: '>'; position: absolute; top: 0; left: 0; font-size: 20px; color: #33cc66; font-weight: 900; }

@media only screen and (max-width: 767px) { .text-list__item:before { font-size: 16px; } }

@media only screen and (max-width: 767px) { .text-list__item { font-size: 16px; } }

